<style lang="stylus">

  @import "./css/variable"

  
  * {
    margin: 0;
    padding: 0;
  }
  html, body {
    height: 100%;
    width: 100%;
    overflow-x: hidden;
  }

  body {
    background-color: #fbf9fe;
  }

  #app {
    display: flex;
    flex-direction: column;
    flex-wrap: nowrap;
    justify-content:space-between;
    height: 100%;
    width: 100%;
  }

  .router-view {
    /*width: 100%;
    padding-top: 46px;*/
    flex-grow: 1;
    flex-shrink: 1;

   /* background-color: red;*/
  }

  .x-header {
    width: 100%;
    height: 46px;
    background-color: $color-my-blue;
    z-index: 100;
    flex-grow: 0;
    flex-shrink: 0;
    position: relative;

    /*  position: absolute;
      left: 0;
      top: 0;*/
  }

  .x-header-left {
    height: 100%;
    /*width: 100px;*/
    position: absolute;
    left: 0;
    top: 0;
    display: flex;
    flex-direction: row;
    align-items: center;
    /*background-color: red;*/
  }

  .x-header-title {
    line-height: 46px;
    text-align: center;
    font-size: 19px;
    font-weight: 400;
    color: white;
  }



  .tab
    display: flex
    height: 44px
    line-height: 44px
    font-size: $font-size-medium-x
    border-top solid 0.5px $color-text-l
    background-color: $color-my-blue
    .tab-item
      flex: 1
      text-align: center
      .tab-link
        padding-bottom: 5px
        color: $color-text-l
      &.router-link-active
        .tab-link
          color: $color-theme
          border-bottom: 2px solid $color-theme

  .component-wrapper-class {
    /*使用这个样式，better-scroll会撑大到和屏幕大小*/
    /*    width: 100%;
        height: 100%;
        background-color: green;
        position: relative;*/

    /*使用这个样式，再加上better-scrolle的包裹组件overflow:hidden 才可以控制better-scroll的大小*/
    width: 100%;
    height: 100%;
    background-color: white;
    display: flex;
    flex-direction: column;
    align-items: center;
    position: relative;
  }

  .bs-wrapper-default-class {
    width: 100%;
    height: 100%;
    /*background-color: yellow;*/
    /*height: 400px;*/
    overflow: hidden;
  }

  .bscroll-content-class {
    width: 100%;
    display: flex;
    justify-content: flex-start;
    flex-direction: column;
    align-items: center;
  }

</style>

<template>
  <div id="app" ref="app">


    <div class="x-header">
      <div class="x-header-left" @click="onClickBack" v-if="showBackBtn">
        <img src="./assets/left-arrow.png" style="height: 60%;"/>
        <span style="color: white; font-size: 18px; line-height: 46px; text-align: center;white-space: nowrap;">返回</span>
      </div>
      <h1 class="x-header-title">
        <span>{{title}}</span>
      </h1>
    </div>

    <keep-alive>
      <router-view class="router-view"></router-view>
    </keep-alive>

    <div class="tab" v-show="isShowTab">
      <router-link tag="div" class="tab-item" to="/home">
        <span class="tab-link">首页</span>
      </router-link>
      <router-link tag="div" class="tab-item" to="/Contact">
        <span class="tab-link">发布</span>
      </router-link>
    </div>

  </div>
</template>

<script>
  import { mapState } from 'vuex'
  export default {
    name:'App',
    methods: {
      onClickBack() {
        console.log('onClickBack...')
        this.$router ? this.$router.back() : window.history.back()
      }
    },
    computed: {
      ...mapState({
        title(state) {
          console.log('state is =>', state)
          return state.pageTitle;
        }
      }),
      showBackBtn(){
        if (this.title=='首页'){
          return 0
        }
        if (this.title=='发布'){
          return 0
        }
        return 1
      },
      isShowTab(){
        if (this.title=='首页'){
          return 1
        }
        if (this.title=='发布'){
          return 1
        }
        return 0
      }
    },
    mounted () {
      // console.log('test =>', testttt)
    }
  }


</script>


