<template>
  <div class="component-wrapper-class">
    <scroll-wrapper class="bs-wrapper-default-class">
      <div class="bscroll-content-class">
        <div v-for="(outline,index) in outlineList"
             @click="onClickOutline(index)"
             class="x-cell">
          <div class="x-location-line">
            <img src="../assets/location.png" style="width: 25px;"/>
            <div style="
          flex: 1;
          justify-content: space-between;
          display: flex;
          flex-direction: row;">
              <span style="">{{outline.LOCATION}}</span>
              <!--<span style="
              white-space: nowrap;
              display: flex;
              flex-direction: column;
              justify-content: center;
              align-items: center;">10-19</span>-->
            </div>
          </div>
          <h1 class="x-outline-title">{{outline.TITLE}}</h1>
        </div>

        <div class="page-menu">
          <!--{{upPageDisable == true?'page-menu-item page-menu-item-disable':'page-menu-item'}}-->
          <div :class="upPageDisable == true?'page-menu-item page-menu-item-disable':'page-menu-item'" @click="onClickUpPage">上一页</div>
         <!-- <div class="page-menu-item">当前1/94页</div>-->
          <!--{{nextPageDisable == true?'page-menu-item page-menu-item-disable':'page-menu-item'}}-->
          <div :class="nextPageDisable == true?'page-menu-item page-menu-item-disable':'page-menu-item'" @click="onClickNextPage">下一页</div>
        </div>



      </div>


    </scroll-wrapper>

   <!-- <div class="refresh-item">
      <img src="../assets/refresh.png" style="height: 50%;width: 50%"/>
    </div>-->
