import Vue from 'vue'
import Router from 'vue-router'
import OutlineList from './views/OutlineList'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      redirect: '/home'
    },
    {
      path: '/home',
      name: '首页',
      component: OutlineList,
      meta:{rank:1.5}
    },
    {
      path: '/HouseDetail',
      name: '详情',
      component: () => import('./views/HouseDetail'),
      meta:{rank:2.5}
    },
    {
      path: '/Contact',
      name: '发布',
      component: () => import('./views/Contact'),
      meta:{rank:1.5}
    }
  ]
})
